.main-nav__item {
  height: 65px;
  border-bottom: 1px solid @light-gray;
  font-size: 20px;
  line-height: 1;
  font-weight: 500;
  color: @black;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-nav__item a {
  text-decoration: none;

  &:link,
  &:visited {
    color: @black;
  }
}

.main-nav__item:not(.main-nav__item--active) a {
  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (max-width: @mobile-width-only) {
  .main-nav--mobile-close {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .main-nav {
    flex-grow: 1;
  }

  .main-nav__list {
    display: flex;
    justify-content: space-between;
    margin-left: 96px;
  }

  .main-nav__item {
    border: none;
    font-size: 18px;
    line-height: 1.39;
    color: @black-two;
    font-weight: normal;
    margin: 0;
    padding: 0;
    height: 58px;

    &--active {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        right: 0;
        bottom: 7px;
        left: 0;
        height: 2px;
        background: @leaf;
      }
    }
  }
}

@media (min-width: @desktop-width) {
  .main-nav__list {
    margin-left: 540px;
  }

  .main-nav__item {
    font-size: 20px;
    line-height: 1.25;
    height: 56px;

    &--active {
      &::after {
        bottom: 5px;
      }
    }
  }

  .main-nav--index {
    .main-nav__item a:link,
    .main-nav__item a:visited {
      color: @white;
    }

    .main-nav__item {
      color: @white;

      &--active {
        &::after {
          background: @white;
        }
      }
    }
  }
}
